/*! common core */
.e-control,
.e-css {
  font-family: "Roboto", "Segoe UI", "GeezaPro", "DejaVu Serif", "sans-serif", "-apple-system", "BlinkMacSystemFont";
  font-size: 12px;
  font-weight: normal;
}

.sf-license-warning {
  background: #e82824;
  color: #fff;
  font-family: 'Segoe UI';
  font-size: medium;
  left: 0;
  line-height: 45px;
  opacity: .75;
  position: fixed;
  right: 0;
  text-align: center;
  top: 0;
  z-index: 999999999;
}

.sf-license-close-btn {
  background: url("data:image/svg+xml;base64,");
  cursor: pointer;
  height: 20px;
  margin-top: 14px;
  padding-top: 10px;
  position: absolute;
  right: 20px;
  width: 20px;
}

.sf-license-warning a,
.sf-license-warning a:hover {
  color: #ffd800;
}

.e-error {
  color: #f44336;
  font-family: "Roboto", "Segoe UI", "GeezaPro", "DejaVu Serif", "sans-serif", "-apple-system", "BlinkMacSystemFont";
  font-size: 12px;
  font-weight: normal;
}

.e-control,
.e-control [class^='e-'],
.e-control [class*=' e-'] {
  box-sizing: border-box;
}

.e-control:focus,
.e-control *:focus {
  outline: none;
}

.e-rtl {
  direction: rtl;
  text-align: right;
}

.e-overlay {
  background-color: #383838;
  filter: alpha(opacity=50);
  height: 100%;
  opacity: .5;
  pointer-events: none;
  touch-action: none;
  width: 100%;
}

.e-hidden {
  display: none;
}

.e-blazor-hidden {
  visibility: hidden;
}

.e-disabled {
  background-image: none;
  cursor: default;
  filter: alpha(Opacity=35);
  opacity: .35;
}

.e-ul {
  list-style-type: none;
}

.e-prevent-select {
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.e-warning {
  color: #ffca1c;
}

.e-success {
  color: #22b24b;
}

.e-information {
  color: #489bd5;
}

.e-block-touch {
  touch-action: pinch-zoom;
}

